// src/App.js
import React from 'react';
import { BrowserRouter as Router, Route, Routes, Link } from 'react-router-dom';
import ComicPage1 from './pages/ComicPage1';
import ComicPage2 from './pages/ComicPage2';
import HomePage from './pages/HomePage';
import './App.css';

function App() {
  return (
    <Router>
      <div className="App">
        <header>
          <h1>JokerWorld</h1>
          <nav className="navbar">
            <Link to="/" className="nav-link"> </Link>
            <Link to="/comic1" className="nav-link"> 1</Link>
            <Link to="/comic2" className="nav-link"> 2</Link>
          </nav>
        </header>
        
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="/comic1" element={<ComicPage1 />} />
          <Route path="/comic2" element={<ComicPage2 />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;
